@import "variables";

.main-part {
  position: relative;
  overflow-x: hidden;
  height: 100vh;
}
.prt_main_wrapper{
  position:relative;
  overflow-x:hidden;
}
.main-part::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #666666;
}
.main-part::-webkit-scrollbar{
  width: 0.1px;
  background-color: #F5F5F5;
}
.main-part::-webkit-scrollbar-thumb{
  background-color: #000000;
}
a {
  color: #717171;
  text-decoration: none;
  cursor:pointer;
}
p{
  color: #717171;
}
a:hover, a:focus {
  color: #717171;
  text-decoration: none;
}
a:focus {
  outline: none;
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  line-height: 1.1;
  color: #202020;
}
img {
  max-width: 100%;
}
input, textarea, select, button {
  outline: none;
  box-shadow:none;
}
input:focus, textarea:focus, select:focus, button:focus {
  outline: none;
  box-shadow:none;
}

.pt-115{
  padding-top: 115px;
}
.pt-80{
  padding-top: 80px;
}
.pt-50{
  padding-top: 50px;
}
.pt-30{
  padding-top: 30px;
}
.pb-115{
  padding-bottom: 115px;
}
.pb-80{
  padding-bottom: 80px;
}
.pb-60{
  padding-bottom: 60px;
}
.pb-50{
  padding-bottom: 50px;
}
.pb-30{
  padding-bottom: 30px;
}
.mb-30{
  margin-bottom: 30px;
}
.btn-part {
  height: 50px;
  line-height: 50px;
  color: #ffffff;
  font-size: 15px;
  font-weight: 400;
  border-radius: 0px;
  padding: 0 20px;
  min-width: 140px;
  outline: none;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-transform: capitalize;
  background-color: $main-color;
  border: 1px solid $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.btn-part:hover, .btn-part:focus {
  color: $main-color;
  background-color: #ffffff;
  border: 1px solid $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
a:hover, a:focus {
  color: #717171;
  text-decoration: none;
}

.heading-part{
  h1 {
    font-size: 40px;
    margin: 0;
    text-transform: uppercase;
    position: relative;
    width: auto;
    font-weight: 600;
    padding-bottom: 15px;
  }
  h1:after {
    content: "";
    position: absolute;
    background-color: #202020;
    width: 100%;
    height: 2px;
    top: 100%;
    left: 0;
  }
  p {
    text-align: right;
    width: auto;
    text-transform: uppercase;
    background-color: #ffffff;
    margin: 0;
    padding-left: 10px;
    position: absolute;
    right: 0;
    bottom: -10px;
    color: $main-color;
    font-weight: 600;
  }
}

.heading-part-content{
  text-align: center;
  .heading-part {
    margin-bottom: 50px;
    display: inline-block;
    position: relative;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
